@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;400;500;700&display=swap);
:root {
    --main-bg-color: #100E1D;
    --sidebar-bg-color: #1E213A;
    
    --text-light: #E7E7EB;
    --text-grey: #A09FB1;
    --text-dark: #110E3C;

    --color-blue: #3C47E9;
    --color-grey: #6E707A;


    --shadow1: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

* {
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
}

body {
    
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100vh;    
}

#root {
    width: 100%;
    height: 100%;    
}

.sidebar_header {
    min-height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 45px;
}

.sidebar_header__search_btn {
    width: 160px;
    height: 40px;
    background: var(--color-grey);
    color: var(--text-light);
    border: none;
    cursor: pointer;    
    box-shadow: var(--shadow1);    
    font-size: 1rem;
    font-weight: 500;
}

.sidebar_header__location_btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    background: var(--color-grey);
    color: var(--text-light);
    box-shadow: var(--shadow1);
    cursor: pointer;    
}
.sidebar__body__weather_preview {
    width: 100%;
    height: 400px;
    display:flex;
    align-items:center;
    justify-content: center;
    position: relative;
    background: url(/devchallenges-weather-app/static/media/Cloud-background.d79dfe61.png) no-repeat center;
}

.sidebar__body__temperature {
    display:flex;
    width:100%;
    align-items: flex-end;
    justify-content:center;
}

.sidebar__body__temperature__degree {
    font-size: 10rem;
    font-weight: 100%;
    color: var(--text-light);
}

.sidebar__body__temperature__unit {
    font-size: 3rem;
    font-weight: 100%;
    color: var(--text-grey);
    margin-bottom:10px;
}

.sidebar__body__weather_description {
    height: 200px;
    font-size: 3rem;
    color: var(--text-grey);
    display: flex;
    align-items:center;
    justify-content: center;    
    font-weight: 600;
}

.sidebar__body__date {
    color: var(--text-grey);
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar__body__date > span {
    margin: 0px 5px;
}

.sidebar__body__location {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    color: var(--text-grey);
    height: 50px;
}
.location_search {
    width: 460px;
    height:100%;
    position: fixed;
    top: 0; left: 0;
    background: var(--sidebar-bg-color);
    display: flex;
    flex-direction: column;
    overflow: auto;
    padding: 0px 20px;
}

.location_search__header {
    height: 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;      
    color: var(--text-light);
    padding: 0px 20px;
}

.location_search__header > .material-icons {
    font-size: 2rem;
    cursor: pointer;    
}

.location_search__results {
    flex: 1 1;
    width:100%;    
    overflow: auto;
    padding: 0px 20px;
}
.location_search__search_bar {
    height: 80px;
    width: 100%;
    padding: 0px 20px;
    display: flex;  
    align-items:center;  
    margin-bottom: 50px;
}

.location_search__search_bar__search_box {
    flex: 1 1;
    margin-right: 10px;
    height: 50px;
    border: 1px solid var(--text-light);
    display: flex;
    align-items:center;
}

.location_search__search_bar__search_box > span {
    width: 30px;
    height: 100%;
    display: flex;  
    align-items:center;  
    justify-content: center;
    color: var(--text-grey);
}

.location_search__search_bar__search_box > input {
    flex: 1 1;
    height: 100%;
    border: none;
    background: inherit;
    color: var(--text-grey);
    outline: none;
    font-size: 1.1rem;
    padding-left: 10px;
}

.location_search__search_bar > button {
    border: none;
    width: 80px;
    height: 50px;
    color: #fff;
    background: var(--color-blue);
    cursor: pointer;
}
.location_search__result_button {
    height: 50px;
    width: 100%;    
    display: flex;
    align-items: center;
    padding: 0px 20px;
    color: var(--text-light);    
    cursor: pointer;
    font-size: 1.2rem;
}

.location_search__result_button:hover {
    border: 1px solid var(--text-grey);
}

.location_search__result_button:hover > .material-icons {
    display: block;
}

.location_search__result_button__text {
    flex: 1 1;
}

.location_search__result_button > .material-icons {
    color: var(--text-grey);    
    display: none;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-360deg);
    }
  }
  @keyframes rotating {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-360deg);
    }
  }
  .rotating {
    -webkit-animation: rotating 2s linear infinite;
    animation: rotating 2s linear infinite;
  }
.sidebar {
    max-width: 460px;
    width: 100%;
    height: 100%;
    background: var(--sidebar-bg-color);
    display: flex;
    flex-direction: column;
    overflow: auto;
    position: relative;
}



.main {
    flex: 1 1;
    background: var(--main-bg-color);
    min-height: 100%;    
}

.main__wrapper {
    max-width: 900px;    
    margin: 0px auto;    
}

.main__units {
    width: 100%;
    height: 80px;
    display:flex;
    justify-content: flex-end;
    align-items: center;    
}

.main__units-btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #585676;
    color: var(--text-light);
    border: none;
    margin: 0px 5px;
    cursor: pointer;
    font-weight: 500;
    font-size: 1.1rem;
}

.main__units-btn-active, .main__units-btn:hover {
    background: var(--text-grey);
    color: var(--text-dark);
}

.weather_card {
    flex:1 1;
    min-width: 160px;
    height: 200px;
    display: flex;
    flex-direction: column;
    padding: 10px 30px;
    background: var(--sidebar-bg-color);
    margin: 0px 10px;
}

.weather_card:first-child {
    margin-left: 0px;
}

.weather_card:last-child {
    margin-right: 0px;
}

.weather_card__date {
    font-size: 1.1rem;
    color: var(--text-light);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
}

.weather_card__icon {
    flex: 1 1;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.weather_card__icon > img {
    width: 56px;
}

.weather_card__temperature {
    display: flex;
    justify-content: space-between;    
    align-items: center;
    height: 40px;
    font-size: 1.2rem;
    font-weight: 700;
}

.weather_card__temp_min {
    color: var(--text-grey);
}
.main__weather_days {
    color: var(--text-light);
    width: 100%;
    display: flex;
}
.todays_highlights {
    width: 100%;
    margin-top: 50px;
}

.todays_highlights > h1 {
    color: var(--text-light);

}

.todays_highlights__content {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.todays_highlights__content > div {
    display: flex;
    flex-direction: column;
    flex-basis: calc(50% - 20px);
}

.todays_highlights__content__item {
    margin: 20px;
    min-height: 200px;
    background: var(--sidebar-bg-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-light);
    padding: 0px 50px;
}

.todays_highlights__content__item:nth-child(odd) {
    margin-left: 0px;
}

.todays_highlights__content__item:nth-child(even) {
    margin-right: 0px;
}

.todays_highlights__content__item > .top {
    font-size: 1.5rem;
    font-weight: 500;
    flex:1.5 1;
    display: flex;
    align-items: center;
}
.todays_highlights__content__item > .middle {
    flex: 2 1;
    display: flex;
    align-items: center;
    font-size: 4rem;
}

.todays_highlights__content__item > .middle > .main-text {
    font-weight: 700;
}
.todays_highlights__content__item > .middle > .secondary-text {
    font-size: 2rem;
    font-weight: 500;
    margin-top: 20px;
}


.todays_highlights__content__item > .bottom {
    flex: 2 1;
    display: flex;    
    align-items: center;
    justify-content: center;
    width: 100%;
}

.todays_highlights__content__item__progress {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    position: relative;
}

.todays_highlights__content__item__progress::-webkit-progress-bar {
    background-color: #fff;
    height: 8px;
    border-radius: 4px;
}

.todays_highlights__content__item__progress::-webkit-progress-value {
    background-color: #FFEC65;
    border-radius: 4px;
}

.todays_highlights__content__item__progress__scale {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}
@-webkit-keyframes rotating /* Safari and Chrome */ {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-360deg);
    }
  }
  @keyframes rotating {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-360deg);
    }
  }
  .rotating {
    -webkit-animation: rotating 2s linear infinite;
    animation: rotating 2s linear infinite;
  }
.app {
    display: flex;
    width: 100%;
    height:100%;
    overflow: hidden;
}
